/*==================
    Portfolio Area 
==================*/
.portfolio {
    position: relative;
    @extend %transition;
    min-height: 600px;
    width: 100%;
    padding-top: 35%;
    
    @media #{$laptop-device} {
        min-height: 500px;
    }
    @media #{$lg-layout} {
        min-height: 500px;
    }
    @media #{$md-layout} {
        min-height: 450px;
    }
    @media #{$sm-layout} {
        min-height: 400px;
    }
    .thumbnail-inner{
        transition: transform .28s ease;
        z-index: 9;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-position: center;
        overflow: hidden;
        border-radius: 5px;
        cursor: pointer;
        &::before{
            background-color: #f6004c;
            background-image: linear-gradient(#f6004c 10%, #000000 100%);
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height : 100%;
            z-index: 5;
            opacity: 0;
            @extend %transition;
        }
    }
    .thumbnail{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-position: center;
        border-radius: 5px;
        background-color: #0a0a0a;
        background-image: url(/assets/images/portfolio/portfolio-1.jpg);
        transform: scale(1.13) translateZ(0);
        backface-visibility: hidden;
        transition: transform .28s ease;
        z-index: 4;
        &.image-1{
            background-image: url(/assets/images/portfolio/portfolio-1.jpg);
        }

        &.image-2{
            background-image: url(/assets/images/portfolio/portfolio-2.jpg);
        }

        &.image-3{
            background-image: url(/assets/images/portfolio/portfolio-3.jpg);
        }

        &.image-4 {
            background-image: url(/assets/images/portfolio/portfolio-4.jpg);
        }
        
        &.image-5 {
            background-image: url(/assets/images/portfolio/portfolio-5.jpg);
        }
        
        &.image-6 {
            background-image: url(/assets/images/portfolio/portfolio-6.jpg);
        }
        
        &.image-7 {
            background-image: url(/assets/images/portfolio/portfolio-7.jpg);
        }
        
        &.image-8 {
            background-image: url(/assets/images/portfolio/portfolio-8.jpg);
        }
        &.image-9 {
            background-image: url(/assets/images/portfolio/portfolio-9.jpg);
        }
        &.image-10 {
            background-image: url(/assets/images/portfolio/portfolio-10.jpg);
        }

        &::after{
            background-color: rgba(45,45,45,.35);
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            content: ' ';
            z-index: 1;
            display: block;
            border-radius: 5px;
            background-color: rgba(25,25,25,.37);
            @extend %transition;
        }
    }

    .bg-blr-image {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-position: center;
        background-color: #0a0a0a;
        background-image: url(/assets/images/portfolio/portfolio-1.jpg);
        @extend %transition;
        transform: translateY(38px);
        opacity: 0;
        z-index: 1;

        &.image-1{
            background-image: url(/assets/images/portfolio/portfolio-1.jpg);
        }
        &.image-2{
            background-image: url(/assets/images/portfolio/portfolio-2.jpg);
        }
        &.image-3{
            background-image: url(/assets/images/portfolio/portfolio-3.jpg);
        }
        &.image-4 {
            background-image: url(/assets/images/portfolio/portfolio-4.jpg);
        }
        &.image-5 {
            background-image: url(/assets/images/portfolio/portfolio-5.jpg);
        }
        &.image-6 {
            background-image: url(/assets/images/portfolio/portfolio-6.jpg);
        }
        &.image-7 {
            background-image: url(/assets/images/portfolio/portfolio-7.jpg);
        }
        &.image-8 {
            background-image: url(/assets/images/portfolio/portfolio-8.jpg);
        }
        &.image-9 {
            background-image: url(/assets/images/portfolio/portfolio-9.jpg);
        }
        &.image-10 {
            background-image: url(/assets/images/portfolio/portfolio-10.jpg);
        }
    }

    .content {
        position: absolute;
        bottom: 40px;
        left: 40px;
        right: 40px;
        max-width: 100%!important;
        z-index: 10;
        padding: 0;
        @media #{$lg-layout} {
            left: 20px;
            right: 20px;
        }
        @media #{$md-layout} {
            left: 20px;
            right: 20px;
        }
        @media #{$sm-layout} {
            left: 20px;
            right: 20px;
        }
        .inner {
            p {
                color: #c6c9d8;
                font-size: 14px;
            }
            h4 {
                font-size: 24px;
                line-height: 36px;
                a{
                    color: #ffffff;
                }
            }
            .title {
                font-size: 24px;
                line-height: 36px;
                color: #ffffff;
            }
            .portfolio-button{
                @extend %transition;
                margin-top: 35px;
                transition: 0.7s;
                @media #{$large-mobile} {
                    margin-top: 25px;
                }
                a {
                    &.rn-btn{
                        color: #ffffff;
                        border-color: rgba(255,255,255,0.3);
                        &:hover{
                            border-color: $theme-color;
                        }
                    }
                }
            }
        }
    }

    &.portfolio-interior-design {
        .thumbnail {
            &.image-1{
                background-image: url(/assets/images/portfolio/interior/portfolio-1.jpg);
            }
    
            &.image-2{
                background-image: url(/assets/images/portfolio/interior/portfolio-2.jpg);
            }
    
            &.image-3{
                background-image: url(/assets/images/portfolio/interior/portfolio-3.jpg);
            }
    
            &.image-4 {
                background-image: url(/assets/images/portfolio/interior/portfolio-4.jpg);
            }
            &.image-5 {
                background-image: url(/assets/images/portfolio/interior/portfolio-5.jpg);
            }
            &.image-6 {
                background-image: url(/assets/images/portfolio/interior/portfolio-6.jpg);
            }
            &.image-7 {
                background-image: url(/assets/images/portfolio/interior/portfolio-7.jpg);
            }
        }
        .bg-blr-image{
            background-image: url(/assets/images/portfolio/interior/portfolio-1.jpg);
            &.image-1{
                background-image: url(/assets/images/portfolio/interior/portfolio-1.jpg);
            }
    
            &.image-2{
                background-image: url(/assets/images/portfolio/interior/portfolio-2.jpg);
            }
    
            &.image-3{
                background-image: url(/assets/images/portfolio/interior/portfolio-3.jpg);
            }
    
            &.image-4 {
                background-image: url(/assets/images/portfolio/interior/portfolio-4.jpg);
            }
            &.image-5 {
                background-image: url(/assets/images/portfolio/interior/portfolio-5.jpg);
            }
            &.image-6 {
                background-image: url(/assets/images/portfolio/interior/portfolio-6.jpg);
            }
            &.image-7 {
                background-image: url(/assets/images/portfolio/interior/portfolio-7.jpg);
            }
        }
    }

    &.text-center {
        .content {
            position: absolute;
            bottom: 40px;
            left: 40px;
            max-width: 80% !important;
            z-index: 10;
            padding: 0;
            right: 40px;
            margin: 0 auto;
            @media #{$lg-layout} {
                left: 20px;
                right: 20px;
            }
            @media #{$md-layout} {
                left: 15px;
                right: 15px;
            }
            @media #{$sm-layout} {
                left: 10px;
                right: 10px;
            }
        }
    }
    &:hover{
        .thumbnail-inner{
            transform: scale(1.08) translateZ(0);
            &::before{
                opacity: 0.85;
            }
        }
        .thumbnail {
            transform: scale(1) translateZ(0);
            &::after{
                background-color: rgba(25,25,25,.24);
            }
        }
        .bg-blr-image{
            opacity: 0;
            z-index: 9;
        }
        
    }
    &.portfolio-style--2{
        .inner{
            margin: 0 20px;
        }
    }
}


